import React from 'react';
import { shallow } from 'enzyme';
import AngularWrapper from './AngularWrapper';

describe('AngularWrapper', () => {
  it('should render', () => {
    const element = shallow(
      <AngularWrapper
        moduleName="plopWrapper"
        component="plop"
      />,
    );

    expect(element).toMatchSnapshot();
  });

  it('should set the proper angular attributes', () => {
    const element = shallow(
      <AngularWrapper
        moduleName="plopWrapper"
        component="plop"
        bindings={{
          foo: 'bar',
        }}
        interpolateBindings={{
          boo: 'faz',
        }}
      />,
    );

    expect(element.instance().component).toEqual(<plop boo="{{boo}}" foo="foo" />);
  });

  it('should update the scope when props are updated', () => {
    const element = shallow(
      <AngularWrapper
        moduleName="plopWrapper"
        component="plop"
        bindings={{
          foo: 'bar',
        }}
        interpolateBindings={{
          boo: 'faz',
        }}
      />,
    );

    element.instance().$rootScope = jest.fn();
    element.instance().angularComponent = {
      $scope: {
        $digest: jest.fn(),
      },
    };

    element.setProps({
      interpolateBindings: {
        boo: 'qaz',
      },
    });
    expect(element.instance().angularComponent.$scope).toMatchObject({
      boo: 'qaz',
      foo: 'bar',
    });
  });
});
